        'E disse: " necessrio que o Filho do homem sofra muitas coisas e seja rejeitado pelos lderes religiosos, pelos chefes dos sacerdotes e pelos mestres da lei, seja morto e ressuscite no terceiro dia".',
        'Jesus dizia a todos: "Se algum quiser acompanhar-me, negue-se a si mesmo, tome diariamente a sua cruz e siga-me.',
