        'Garanto-lhes que alguns que aqui se acham de modo nenhum experimentaro a morte antes de verem o Reino de Deus".',
        'Aproximadamente oito dias depois de dizer essas coisas, Jesus tomou consigo a Pedro, Joo e Tiago e subiu a um monte para orar.',
