        'Apareceram em glorioso esplendor, e falavam sobre a partida de Jesus, que estava para se cumprir em Jerusalm.',
        'Pedro e os seus companheiros estavam dominados pelo sono; acordando subitamente, viram a glria de Jesus e os dois homens que estavam com ele.',
        'Quando estes estavam se retirando, Pedro disse a Jesus: "Mestre,  bom estarmos aqui. Faamos trs tendas: uma para ti, uma para Moiss e uma para Elias". ( Ele no sabia o que estava dizendo. )',
        'Enquanto ele estava falando, uma nuvem apareceu e os envolveu, e eles ficaram com medo ao entrarem na nuvem.',
