        'Naquela hora Jesus, exultando no Esprito Santo, disse: "Eu te louvo, Pai, Senhor do cu e da terra, porque escondeste estas coisas dos sbios e cultos e as revelaste aos pequeninos. Sim, Pai, pois assim foi do teu agrado.',
        '"Todas as coisas me foram entregues por meu Pai. Ningum sabe quem  o Filho, a no ser o Pai; e ningum sabe quem  o Pai, a no ser o Filho e aqueles a quem o Filho o quiser revelar".',
