        'Certa ocasio, um perito na lei levantou-se para pr Jesus  prova e lhe perguntou: "Mestre, o que preciso fazer para herdar a vida eterna? "',
        '"O que est escrito na Lei? ", respondeu Jesus. "Como voc a l? "',
