        'Aumentando a multido, Jesus comeou a dizer: "Esta  uma gerao perversa. Ela pede um sinal miraculoso, mas nenhum sinal lhe ser dado, exceto o sinal de Jonas.',
        'Pois assim como Jonas foi um sinal para os ninivitas, o Filho do homem tambm o ser para esta gerao.',
